import Button from '@nextcloud/vue/dist/Components/Button';
import PackageUp from 'vue-material-design-icons/PackageUp';
import { getFilePickerBuilder } from '@nextcloud/dialogs';
import { APP_ID } from '../shared/constants';
var picker = getFilePickerBuilder(t('files', 'Choose a file to import')).setMultiSelect(false) // TODO add custom mime type for user_migration files?
// .setMimeTypeFilter([])
.setModal(true).setType(1).allowDirectories(false) // TODO start at default export folder path?
// .startAt('')
.build();
export default {
  name: 'ImportSection',
  components: {
    Button: Button,
    PackageUp: PackageUp
  },
  data: function data() {
    return {
      filePickerError: null,
      APP_ID: APP_ID
    };
  },
  methods: {
    pickImportFile: function pickImportFile() {
      var _this = this;

      this.filePickerError = null;
      picker.pick().then(function (filePath) {
        _this.logger.debug("path ".concat(filePath, " selected for import"));

        if (!filePath.startsWith('/')) {
          throw new Error(t(APP_ID, 'Invalid import file selected'));
        }
      }).catch(function (error) {
        _this.logger.error("Selecting file for import aborted: ".concat(error.message || 'Unknown error'), {
          error: error
        });

        _this.filePickerError = error.message || t(APP_ID, 'Unknown error');
      });
    }
  }
};